@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .HButton {
        @apply flex items-center justify-center text-white h-9 w-auto px-3 font-title rounded-md hover:bg-lightBrown hover:cursor-pointer hover:shadow-md transition-all hover:scale-105 duration-300;

    }

    .Dropdownbutton {
        @apply flex items-center justify-center text-white h-9 w-auto px-3 font-title rounded-md hover:bg-lightBrown hover:cursor-pointer hover:shadow-md transition-all;

    }

    .DropdownbuttonActiv {
        @apply flex items-center justify-center text-white h-9 w-auto px-3 font-title rounded-md rounded-b-none bg-lightBrown cursor-pointer shadow-md duration-300;

    }

    .Dropdownitem {
        @apply flex-1 bg-slate-500 h-10 w-full;
    }
}